@import url(https://fonts.googleapis.com/css?family=family=Archivo+Black|Judson:400,700);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.head {
  position: absolute;
  background-image: url(/static/media/layer0.45a54b54.svg);
  background-repeat: no-repeat;
  background-size: cover;
  height: 61em;
  width: 100%;
  z-index: 0;
}

.layer1 {
  position: absolute;
  background-image: url(/static/media/layer1.28acf5bf.svg);
  background-repeat: no-repeat;
  background-size: cover;
  height: 61em;
  width: 100%;
  z-index: 2;
}

.layer4 {
  position: relative;
  background-image: url(/static/media/layer4.911efb87.svg);
  background-repeat: no-repeat;
  background-size: cover;
  height: 40em;
  width: 13em;
  margin-left: 22.1em;
  margin-top: 9.6em;
  z-index: 4;
}

.name {
  display: inline-block;
  color: white;
  width: 100%;
  opacity: 0;
  text-align: left;
  padding-top: 0.5em;
  padding-left: 0.5em;
  font-size: 2em;
  font-family: 'Archivo Black', sans-serif;
  letter-spacing: 0.2em;
  font-weight: 600;
  height: auto;

  -webkit-animation: fadeIn 2s ease-out forwards;

          animation: fadeIn 2s ease-out forwards;
  -webkit-animation-delay: 1s;
          animation-delay: 1s;
}

.sub-title {
  color: white;
}

.left-clouds {
  position: absolute;
  background-image: url(/static/media/left_clouds.02a2333d.svg);
  background-repeat: no-repeat;
  background-size: cover;
  left: 0;
  height: 16em;
  width: 30em;
  margin-top: 9.6em;
  z-index: 3;

  -webkit-animation: moveInRight 1.5s ease-out;

          animation: moveInRight 1.5s ease-out;
}

.right-clouds {
  position: absolute;
  background-image: url(/static/media/right_clouds.a5721831.svg);
  background-repeat: no-repeat;
  background-size: cover;
  height: 24em;
  width: 70em;
  right: 0;
  margin-top: 5.6em;
  z-index: 1;

  -webkit-animation: moveInLeft 1.5s ease-out;

          animation: moveInLeft 1.5s ease-out;
}

svg {
  position: absolute;
}

@-webkit-keyframes moveInRight {
  /* Initial state */
  0% {
      opacity: 0;
      -webkit-transform: translateX(-20em);
              transform: translateX(-20em);
  }

  /* Final state */
  100% {
      opacity: 1;
      -webkit-transform: translate(0);
              transform: translate(0);
  }
}

@keyframes moveInRight {
  /* Initial state */
  0% {
      opacity: 0;
      -webkit-transform: translateX(-20em);
              transform: translateX(-20em);
  }

  /* Final state */
  100% {
      opacity: 1;
      -webkit-transform: translate(0);
              transform: translate(0);
  }
}

@-webkit-keyframes moveInLeft {
  /* Initial state */
  0% {
      opacity: 0;
      -webkit-transform: translateX(20em);
              transform: translateX(20em);
  }

  /* Final state */
  100% {
      opacity: 1;
      -webkit-transform: translate(0);
              transform: translate(0);
  }
}

@keyframes moveInLeft {
  /* Initial state */
  0% {
      opacity: 0;
      -webkit-transform: translateX(20em);
              transform: translateX(20em);
  }

  /* Final state */
  100% {
      opacity: 1;
      -webkit-transform: translate(0);
              transform: translate(0);
  }
}

@-webkit-keyframes fadeIn {
  /* Initial state */
  0% {
      opacity: 0;
  }

  /* Final state */
  100% {
      opacity: 1;
  }
}

@keyframes fadeIn {
  /* Initial state */
  0% {
      opacity: 0;
  }

  /* Final state */
  100% {
      opacity: 1;
  }
}
.body {
  background-color: #322e49;
  height: 80em;
}
body {
  height: 100%;
  width: 100%;
}

.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

